import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import axios from "axios";
import orderBy from "lodash/orderBy";
import get from "lodash/get";
import { catOptions } from "../components/CategorySelector";
import BotState from "../components/BotState";
import { splitArray } from "../utils";
import isEmpty from "lodash/isEmpty";
import useTabState from "../hook/useTabState";

const catObj = catOptions.reduce((ac, cu) => ({ ...ac, [cu.value]: cu.label }), {});
const getTimeStr = date => {
  const dateObj = new Date(date);
  return `${dateObj.toLocaleTimeString()} ${dateObj.toLocaleDateString()}`;
};

export default function Task() {
  const [data, setdata] = useState({});
  const history = useHistory();
  const [loading, setLoading] = useState(false);
  const [loadingText, setLoadingText] = useState("Loading...");
  const [botState, setBotState] = useState({});
  const { isActive } = useTabState();

  const [selectedItems, setSelectedItems] = useState([]);
  const [filter, setfilter] = useState({});

  // ['asc', 'desc']
  const [sort, setSort] = useState({ by: "", order: "asc" });

  const { tasks: allTask = [] } = data;
  const filterKeys = Object.keys(filter);
  const filterTasks = filterKeys.length ? allTask.filter(e => e[filterKeys[0]].includes(filter[filterKeys[0]])) : allTask;
  const tasks = orderBy(filterTasks, sort.by, sort.order);

  const handleSort = col => {
    const temp = sort.by === col && sort.order === "asc" ? "desc" : "asc";
    setSort({ by: col, order: temp });
  };

  const getData = async () => {
    setLoading(true);
    const { data: resData = {} } = await axios.get("/api/v1/task/list");
    setdata(resData);
    setLoading(false);
  };

  const getProcesStateData = async () => {
    const { data: resData = {} } = await axios.get("/api/v1/task/processStats");
    setBotState(resData);
  };

  useEffect(() => {
    getData();
    getProcesStateData();
    // eslint-disable-next-line
  }, []);

  useEffect(() => {
    if (isActive && !loading) {
      const refreshTime = isEmpty(botState) ? 30 * 1000 : 10 * 1000;
      const timer = setTimeout(getProcesStateData, refreshTime);
      return () => clearTimeout(timer);
    }
  }, [botState, isActive, loading]);

  return (
    <main>
      <div className="content">
        <div className="d-flex">
          <div className="d-flex flex-1">
            <h1>Tasks List</h1>
            <button style={{ marginLeft: 20 }} className="danger" onClick={getData}>
              Refresh
            </button>
          </div>
          {!!selectedItems.length && (
            <button
              disabled={loading}
              onClick={async () => {
                try {
                  setLoading(true);
                  const selectedTask = tasks.filter(e => selectedItems.includes(e._id));
                  const [finalArr, pageSize] = splitArray(selectedTask);
                  for (const iterator of finalArr) {
                    try {
                      const activeIndex = finalArr.indexOf(iterator);
                      setLoadingText(`Starting bot on ${activeIndex * pageSize}/${selectedTask.length}`);
                      const { data } = await axios.post("/api/v1/task/start", iterator);
                      console.log("tasks response===", data);
                    } catch (error) {
                      // setLoadingText(`Starting bot on ${1 * pageSize}/${selectedTask.length}`);
                    }
                  }
                  setLoading(false);
                  setLoadingText("Loading...");
                  getData();
                } catch (error) {
                  setLoading(false);
                  getData();
                }
              }}
            >
              {`Send ${selectedItems.length} chats`}
            </button>
          )}
          <button onClick={() => history.push("/task/upsert")}>Add</button>
        </div>
        <BotState botState={botState} />
        <div className="d-flex align-center" style={{ padding: "15px 0px" }}>
          <div>Filters: </div>
          <button
            className={get(filter, "linkType", "").includes("mdisk") ? "" : "outline"}
            onClick={() => {
              setSelectedItems([]);
              setfilter(get(filter, "linkType", "").includes("mdisk") ? {} : { linkType: "mdisk" });
            }}
          >
            Mdisk
          </button>
          <button
            className={get(filter, "linkType", "").includes("vivdisk") ? "" : "outline"}
            onClick={() => {
              setSelectedItems([]);
              setfilter(get(filter, "linkType", "").includes("vivdisk") ? {} : { linkType: "vivdisk" });
            }}
          >
            Vivdisk
          </button>
          <button
            className={get(filter, "status", "").includes("processing") ? "" : "outline"}
            onClick={() => {
              setSelectedItems([]);
              setfilter(get(filter, "status", "").includes("processing") ? {} : { status: "processing" });
            }}
          >
            processing
          </button>
        </div>
        <table className="task">
          <thead>
            <tr>
              <th>
                <input
                  type="checkbox"
                  checked={selectedItems.length === tasks.length}
                  onChange={e => {
                    setSelectedItems(e.target.checked ? tasks.map(e => e._id) : []);
                  }}
                />
              </th>
              <th>S.No</th>
              <th onClick={() => handleSort("groupInfo.title")}>Group Name</th>
              <th onClick={() => handleSort("category")}>Category</th>
              <th onClick={() => handleSort("size")}>Size</th>
              <th onClick={() => handleSort("page")}>Send Page</th>
              <th onClick={() => handleSort("pageIncrementor")}>Page Incrementor</th>
              <th onClick={() => handleSort("channelName")}>Channel Name</th>
              <th onClick={() => handleSort("linkType")}>link Type</th>
              <th onClick={() => handleSort("cname")}>Cname</th>
              <th onClick={() => handleSort("lastExecuted")}>Last Executed</th>
              <th onClick={() => handleSort("status")}>Status</th>
            </tr>
          </thead>
          <tbody>
            {tasks &&
              tasks.map((el, index) => (
                <tr key={`tassk-${index}`}>
                  <td>
                    <input
                      type="checkbox"
                      checked={selectedItems.includes(el._id)}
                      onChange={e => {
                        setSelectedItems(e.target.checked ? [...selectedItems, el._id] : selectedItems.filter(e => e !== el._id));
                      }}
                    />
                  </td>
                  <td>{index + 1}</td>
                  <td>{`${el?.groupInfo?.title} (${el?.groupInfo?.id.toString().slice(4)}) \n(${el.membersCount || ""})`}</td>
                  <td>{el.categoryState.map(e => `${catObj[e.category]} \n`)}</td>
                  <td>{el.categoryState.reduce((ac, e) => ac + +e.size, 0)}</td>
                  <td>
                    {el.categoryState.map(e => (
                      <div>{e.page}</div>
                    ))}
                  </td>
                  <td>
                    {el.categoryState.map(e => (
                      <div>{e.pageIncrementor}</div>
                    ))}
                  </td>
                  <td>{el.channelName}</td>
                  <td>{el.linkType}</td>
                  <td>{el.cname}</td>
                  <td>{el.lastExecuted && getTimeStr(el.lastExecuted)}</td>
                  <td>
                    <>
                      {/* <button
                          disabled={loading}
                          onClick={async () => {
                            try {
                              setLoading(true);
                              const { data } = await axios.post("/api/v1/task/start", [el]);
                              console.log("start response===", data);
                              setLoading(false);
                            } catch (error) {
                              setLoading(false);
                            }
                          }}
                        >
                          Start
                        </button> */}
                      <button
                        className="btn icon-only"
                        onClick={() =>
                          history.push({
                            pathname: "/task/upsert",
                            state: el
                          })
                        }
                      >
                        Edit
                      </button>
                      <div>{el.status === "active" ? "" : el.status}</div>
                    </>
                  </td>
                </tr>
              ))}
          </tbody>
        </table>
        {loading && (
          <div className="d-flex loading-box">
            <div className="loading-div"></div>
            <div>{loadingText}</div>
          </div>
        )}
      </div>
    </main>
  );
}
